<template>
    <div class="login-container-layout">
        <div class="login-container">
            <form novalidate @submit.prevent="validate">
                <ul class="input-ul">
                    <li>
                        <div class="label">이름</div>
                        <v-text-field
                                :error-messages="nameErrors"
                                outlined
                                @input="$v.name.$touch()"
                                @blur="$v.name.$touch()"
                                placeholder="이름을 입력해주세요"
                                v-model="name"/>
                    </li>
                    <li v-if="role=='ROLE_MANAGER'">
                        <div class="label">법인명</div>
                        <div class="auth-item">
                            <div class="input-text-box">
                                <v-text-field
                                        v-model="busiName"
                                        :error-messages="busiNameErrors"
                                        outlined
                                        @input="$v.busiName.$touch()"
                                        @blur="$v.busiName.$touch()"
                                        placeholder="법인명을 입력해주세요"/>
                            </div>
                        </div>
                    </li>
                    <li>
                        <div class="label">아이디</div>
                        <div class="auth-item">
                            <div class="input-text-box">
                                <v-text-field
                                        v-model="uid"
                                        outlined
                                        disabled
                                />
                            </div>
                        </div>

                    </li>
                    <li>
                        <div class="label">이메일</div>
                        <div class="auth-item">
                            <div class="input-text-box">
                                <v-text-field
                                        v-model="email"
                                        :error-messages="emailErrors"
                                        outlined
                                        @input="$v.email.$touch()"
                                        @blur="$v.email.$touch()"
                                        placeholder="이메일을 입력해주세요"/>

                            </div>
                        </div>
                    </li>

                    <li>
                        <div class="label">닉네임(선택)</div>
                        <div class="input-text-box">
                            <v-text-field
                                    :error-messages="nickErrors"
                                    outlined
                                    @input="$v.nick.$touch()"
                                    @blur="$v.nick.$touch()"
                                    placeholder="닉네임을 입력해주세요"
                                    v-model="nick"/>

                        </div>
                    </li>
                    <li>
                        <div class="check-box">
                            <v-checkbox
                                    color="#000"
                                    label="이메일 수신동의"
                                    value="yes"
                                    v-model="emailSend"/>

                        </div>
                    </li>

                    <li>
                        <div class="check-box">
                            <v-checkbox
                                    color="#000"
                                    label="인증여부"
                                    value="yes"
                                    v-model="auth"/>

                        </div>
                    </li>
                    <li v-if="uout=='yes'">
                        <div style="color:#880E4F;font-weight: bold;font-size:18px;">
                          탈퇴회원
                        </div>
                    </li>

                </ul>
                <div class="button-row">
                    <v-btn type="button" @click="$emit('close')" depressed rounded class="white-button">
                        창닫기
                    </v-btn>
                    <v-btn type="submit" v-if="uout!='yes'" depressed rounded class="black-button">
                        정보수정
                    </v-btn>
                </div>
            </form>

        </div>
    </div>
</template>
<script>
    import {validationMixin} from 'vuelidate'
    import {
        required,
        email,
    } from 'vuelidate/lib/validators'
    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_MEMBER_UPDATE
    } from '@/config/admin/urls'

    export default {
        props: ["info"],
        mixins: [validationMixin],
        validations: {
            email: {required, email},
            name: {required},
            nick: {},
            busiName: {required}
        },
        computed: {
            busiNameErrors() {
                const errors = []
                if (!this.$v.busiName.$dirty) return errors
                !this.$v.busiName.required && errors.push('법인명을 입력하세요')
                return errors;
            },
            emailErrors() {
                const errors = []
                if (!this.$v.email.$dirty) return errors
                if (!this.$v.email.required) {
                    errors.push('이메일을 입력하세요')
                }
                if (!this.$v.email.email) {
                    errors.push('잘못된 형식의 이메일 입니다')
                }
                return errors;

            },
            nameErrors() {
                const errors = []
                if (!this.$v.name.$dirty) return errors
                !this.$v.name.required && errors.push('이름을 입력하세요')
                return errors;
            },
            nickErrors() {
                const errors = []
                if (!this.$v.nick.$dirty) return errors
                this.nickError = false;
                if (this.nick) {
                    const reg = /^[ㄱ-ㅎ|가-힣|a-z|A-Z|0-9|]{2,10}$/;
                    if (!reg.test(this.nick)) {
                        errors.push('닉네임은 한글,영문,숫자 2~10자 만 가능합니다')
                        this.nickError = true;
                    }

                }
                return errors;
            },

        },
        data() {
            return {
                serverFlag: false,
                busiName: '',
                uid: '',
                name: '',
                email: '',
                nick: '',
                emailSend: '',
                nickError: false,
                emailError: false,
                role: '',
                id: '',
                auth:'',
                uout:'no',
            };
        },
        created() {
            this.role = this.info.role;
            console.log(this.info)
            this.busiName = this.info.busiName
            this.uid = this.info.uid
            this.name = this.info.name
            this.email = this.info.email
            this.nick = this.info.nickName
            this.emailSend = this.info.emailSend
            this.auth  = this.info.auth
            this.uout  = this.info.uout

        },
        methods: {
            validate() {
                if (this.serverFlag) return;
                this.$v.$touch();
                let flag = false;
                for (const field in this.$v.$params) {
                    if (this.role == 'ROLE_MEMBER') {
                        if (field != 'busiName') {
                            if (this.$v[field].$anyError) flag = true;
                        }
                    } else {
                        if (this.$v[field].$anyError) {
                            flag = true;
                        }
                    }

                }
                if (flag) return;
                if (this.nick) {
                    if (this.nickError) {
                        return;
                    }
                }
                const frm = {
                    name: this.name,
                    email: this.email,
                    nick: this.nick,
                    emailSend: ((this.emailSend)?this.emailSend:'no'),
                    busiName: this.busiName,
                    uid:this.uid,
                    auth:((this.auth)?this.auth:'no')
                }
                this.$eventBus.$emit('overlay', 'open');
                this.serverFlag = true;
                apiService.post(API_ADMIN_MEMBER_UPDATE, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    let result = res.status;
                    let params = {}
                    if (result == 'success') {
                        params = {
                            message: '정보가 수정되었습니다',
                            doAction: () => {
                                this.$emit('reload');
                            }
                        };

                    } else {
                        let msg = '';
                        switch (res.code) {
                            case 'doubleEmail':
                                msg = '중복된 이메일 입니다'
                                break;
                            case 'doubleNick':
                                msg = '중복된 닉네임 입니다'
                                break;
                            default:
                                msg = '잘못된 접근입니다'
                                break;
                        }
                        params = {
                            message: msg,
                        };
                    }
                    this.$eventBus.$emit('modalOpen', params);

                })
            },
        },
        mounted() {
        }

    }
</script>


<style lang="scss" scoped>
    .login-container {
        width: 100%;
        margin: 0 auto;
        padding: 36px 40px 50px 40px;
        background: #fff;

        .w-full {
            width: 100%;
            margin: auto;

            .input-box {
                .label {
                    font-size: 14px;
                    color: #333;
                    padding-bottom: 5px;
                }

                width: 100%;
                margin: auto;
            }
        }

        .button-row {
            width: 100%;
            padding: 20px 0 20px 0;
            text-align: center;

            .black-button {
                background: #000;
                color: #fff;
                margin-left: 20px;
            }
        }

        .input-ul {
            padding: 0;
            margin: 0;

            li {
                .label {
                    padding: 0 0 5px 0;
                    font-weight: 600;
                    color: #000;
                    font-size: 14px;
                }

                .input-text-box {
                    width: 100%;
                    position: relative;

                    .error-message {
                        position: absolute;
                        bottom: 8px;
                        left: 10px;
                        color: #880E4F;
                        font-size: 12px;
                    }
                }

                .check-box {
                    margin-top: -10px;

                    ::v-deep .v-label {
                        font-size: 14px;
                        color: #333;
                        margin-left: -5px;
                    }
                }

                .auth-item {
                    display: flex;

                    .input-text-box {
                        width: 100%;
                        position: relative;

                        .error-message {
                            position: absolute;
                            bottom: 8px;
                            left: 10px;
                            color: #880E4F;
                            font-size: 12px;
                        }
                    }

                    .button-box {
                        margin-left: 8px;
                        min-width: 130px;

                        .send-button {
                            width: 100%;
                            color: #fff !important;
                            height: 54px !important;
                            font-size: 11px !important;
                        }
                    }

                }
            }
        }

    }

</style>
