<template>
    <div class="coupon-list">
        <div class="table-box">
            <form novalidate @submit.prevent="search">
                <div class="search-box">
                    <div class="sub-title" style="padding-left:10px;">회원 검색</div>
                    <table class="table-search" cellspacing="0" cellpadding="0" border="0">
                        <tbody>
                        <tr>
                            <th style="border-radius:5px">회원구분</th>
                            <td style="border-bottom:solid 1px #999;border-bottom:solid 1px #999;height:50px;">
                                <div style="max-height:40px;margin-top:-10px;">
                                    <v-radio-group
                                            v-model="mstatus"
                                            dense
                                            row
                                    >
                                        <v-radio
                                                label="전체"
                                                value="all"
                                        ></v-radio>
                                        <v-radio
                                                label="탈퇴회원"
                                                value="out"
                                        ></v-radio>
                                    </v-radio-group>
                                </div>
                            </td>
                            <th style="border-left:solid 1px #999;border-bottom:solid 1px #999;">인증여부</th>
                            <td style="border-bottom:solid 1px #999;">
                                <div style="display: flex;max-height:40px;margin-top:-25px;">
                                    <v-checkbox v-model="auth" value="yes" label="YES"></v-checkbox>
                                    <span style="padding:0 10px;">&nbsp;</span>
                                    <v-checkbox v-model="auth" value="no" label="NO"></v-checkbox>
                                </div>
                            </td>
                            <th style="border-left:solid 1px #999;border-bottom:solid 1px #999;">이메일 수신동의</th>
                            <td style="border-bottom:solid 1px #999;">
                                <div style="display: flex;max-height:40px;margin-top:-25px;">
                                    <v-checkbox v-model="emailSend" value="yes" label="YES"></v-checkbox>
                                    <span style="padding:0 10px;">&nbsp;</span>
                                    <v-checkbox v-model="emailSend" value="no" label="NO"></v-checkbox>
                                </div>
                            </td>

                        </tr>
                        <tr>
                            <th>검색어</th>
                            <td colspan="5">

                                <div class="select-box" style="display: flex;max-height:40px;padding-top:6px;">
                                    <v-select v-model="keywordCmd"
                                              dense
                                              style="max-width:100px;"
                                              :items="keywordCmdList">

                                    </v-select>
                                    <div class="input-box" style="margin-left:20px;width:300px;">
                                        <v-text-field
                                                dense
                                                v-model="keyword"
                                        ></v-text-field>
                                    </div>
                                </div>
                            </td>

                        </tr>
                        <tr>
                            <th style="border-radius:5px">등록일 검색</th>
                            <td colspan="5">
                                <div style="display: flex;max-height: 40px;padding-top:6px;">
                                    <div class="date-box">
                                        <v-btn depressed :class="'white-button '+dayClass.to" @click="setDay('to')">오늘
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.yer" @click="setDay('yer')">
                                            어제
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.thr" @click="setDay('thr')">
                                            3일
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.week" @click="setDay('week')">
                                            1주일
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.mon" @click="setDay('mon')">
                                            1개월
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.tmon" @click="setDay('tmon')">
                                            3개월
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.all" @click="setDay('all')">
                                            전체
                                        </v-btn>

                                    </div>
                                    <div style="display: flex;margin-top:-11px;margin-left:10px;">
                                        <v-menu
                                                v-model="vmenu1"
                                                :close-on-content-click="false"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="auto"
                                        >
                                            <template v-slot:activator="{ on, attrs }">
                                                <div class="input-box" style="width:120px;">
                                                    <v-text-field
                                                            v-model="stdate"
                                                            readonly
                                                            v-bind="attrs"
                                                            v-on="on"
                                                    ></v-text-field>
                                                </div>
                                            </template>
                                            <v-date-picker
                                                    v-model="stdate"
                                                    @input="vmenu1 = false"
                                            ></v-date-picker>
                                        </v-menu>

                                        <div style="width:30px;text-align: center;line-height: 50px;height:50px;">~
                                        </div>

                                        <v-menu
                                                v-model="vmenu2"
                                                :close-on-content-click="false"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="auto"
                                        >
                                            <template v-slot:activator="{ on, attrs }">
                                                <div class="input-box" style="width:120px;">
                                                    <v-text-field
                                                            v-model="endate"
                                                            readonly
                                                            v-bind="attrs"
                                                            v-on="on"
                                                    ></v-text-field>
                                                </div>
                                            </template>
                                            <v-date-picker
                                                    v-model="endate"
                                                    @input="vmenu2 = false"
                                            ></v-date-picker>
                                        </v-menu>
                                    </div>
                                </div>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </div>
                <div class="button-row">
                    <v-btn class="white-button" depressed @click="searchReset()"><span>초기화</span></v-btn>
                    <v-btn class="search-button" type="submit" depressed><span>검색</span></v-btn>
                </div>
            </form>
            <div style="display: flex;padding:0 10px;justify-content: space-between;z-index: 20;">
                <div class="sub-title" style="display:flex;">회원 리스트
                    <v-btn depressed color="primary" outlined class="sdel-button" style="margin-top:-5px;" rounded
                           @click="deleteMember()">
                        선택삭제
                    </v-btn>
                    <div class="download-row" style="padding-left:30px;margin-top:-8px;">
                        <input type="file" style="display: none;" ref="file" accept=".xlsx" @change="fileUpload"/>

                        <v-btn type="button" depressed class="black-button" @click="excelDown()">검색결과 전체{{roleName}}회원
                            엑셀다운로드
                        </v-btn>

                        <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn type="button" depressed class="white-button"
                                       v-bind="attrs"
                                       v-on="on"
                                       @click="excelUpload()">{{roleName}}회원 엑셀업로드
                                </v-btn>
                            </template>
                            <div>
                                <div class="sample-title">{{roleName}}회원 엑셀입력 샘플</div>
                                <table class="sample-excel" style="background: #fff;">
                                    <thead>
                                    <tr>
                                        <th>아이디</th>
                                        <th>비밀번호</th>
                                        <th style="width:100px;">이름</th>
                                        <th style="width:100px;">이메일</th>
                                        <th>닉네임</th>
                                        <th style="width:150px;">이메일수신동의</th>
                                        <th v-if="this.role == 'busi'" style="width:150px;">법인명</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr>
                                        <td>test33</td>
                                        <td>a111111</td>
                                        <td>김철수</td>
                                        <td>test33@naver.com</td>
                                        <td>홍길동</td>
                                        <td>yes</td>
                                        <td v-if="role == 'busi'">삼성산업</td>
                                    </tr>
                                    <tr>
                                        <td>test34</td>
                                        <td>a111111</td>
                                        <td>박철순</td>
                                        <td>test34@naver.com</td>
                                        <td></td>
                                        <td>no</td>
                                        <td v-if="role == 'busi'">대우산업</td>

                                    </tr>
                                    </tbody>
                                </table>

                            </div>
                        </v-tooltip>

                    </div>
                </div>
                <div class="select-box" style="display:flex;max-height:40px;padding-top:4px;">
                    <div class="total">전체 회원수 {{ util.numberFormat(paging.total) }}명</div>
                    <v-select v-model="datalimit"
                              dense
                              style="max-width:100px;"
                              :items="datalimitList">

                    </v-select>

                </div>
            </div>
            <shopTable v-if="tableShow" :datas="memberList" :fixed="fixed" @pageMove="pageMove"
                       @parentEvent="tableEventAction" :columns="columns" :paging="paging"
                       @checkDataList="checkDataList" @memberView="memberView"></shopTable>

        </div>
        <v-dialog
                v-model="memberDialog"
                width="600"
                persistent
                rounded
                style="z-index:100;"
        >
            <div class="modal-layout" style="background: #fff;">
                <div class="modal-header">
                    <div class="modal-title">회원정보 보기</div>
                    <div class="modal-close" @click="memberDialog=false">
                        <font-awesome-icon icon="fa-solid fa-circle-xmark"/>
                    </div>
                </div>
                <div class="modal-body">

                    <memberModify @close="memberDialog=false" @reload="reload" :info="selectedMember"
                                  v-if="memberDialog"></memberModify>

                </div>
            </div>
        </v-dialog>

        <div style="display:none">
            <table border="1" id="excelTable">
                <thead>
                <tr>
                    <th>번호</th>
                    <th v-if="this.role == 'busi'">법인명</th>
                    <th>이름</th>
                    <th>아이디</th>
                    <th>이메일</th>
                    <th>닉네임</th>
                    <th>이메일수신여부</th>
                    <th>인증여부</th>
                    <th>탈퇴여부</th>
                    <th>등록일</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item,index) in excelList">
                    <td>{{index+1}}</td>
                    <td v-if="role == 'busi'">{{item.busiName}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.uid}}</td>
                    <td>{{item.email}}</td>
                    <td>{{item.nickName}}</td>
                    <td>{{item.emailSendView}}</td>
                    <td>{{item.auth}}</td>
                    <td>{{item.uout}}</td>
                    <td>{{item.createAt}}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>


<script>
    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_GET_MEMBER_ALL_LIST,
        API_ADMIN_UPLOAD_MEMBER_EXCEL,
        API_ADMIN_DELETE_MEMBER

    } from '@/config/admin/urls'
    import UTIL from '@/common/util'
    import shopTable from '@/components/shopTable'
    import memberModify from './memberModify'

    export default {
        components: {
            shopTable,
            memberModify
        },
        props: ["accept"],
        watch: {
            '$route'(to, from) {
                this.tableShow = false;
                setTimeout(() => {
                    this.tableShow = true;
                    this.setInit();
                }, 100)

            },
            datalimit(val) {
                this.getDataList(1);
            },
        },
        computed: {
            form() {
                return {
                    point: this.point

                }
            },
        },
        data() {
            return {
                util: UTIL,
                columns: [],
                ids: [],
                fixed: 0,
                memberList: [],
                paging: {
                    total: '',
                    links: [],
                    currentPage: 1,
                    lastPage: 1,
                },
                page: 1,
                datalimit: 20,
                dataSort: {
                    field: 'id',
                    sort: 'desc'
                },
                keywordCmd: 'name',
                keywordCmdList: [
                    {text: '이름', value: 'name'},
                    {text: '아이디', value: 'uid'},

                ],
                keyword: '',
                stdate: '',
                endate: '',
                vmenu1: false,
                vmenu2: false,
                datalimitList: [
                    {text: '10개', value: 10},
                    {text: '20개', value: 20},
                    {text: '30개', value: 30},
                    {text: '50개', value: 50},
                    {text: '100개', value: 100},
                ],
                dayClass: {
                    all: 'active',
                    to: '',
                    yer: '',
                    thr: '',
                    week: '',
                    mon: '',
                    tmon: '',
                },
                excelList: [],
                selectedId: '',
                mstatus: 'all',
                auth: [],
                role: 'indi',
                emailSend: [],
                tableShow: true,
                selectedMember: {},
                memberDialog: false,
                roleName: '개인',
                excelDownActive: false,


            }
        },
        created() {
            //if (this.accept) this.mstatus = this.accept;
            this.setInit();
        },
        methods: {
            setInit() {
                this.role = this.$route.params.role;
                this.setColumn();
                let item = {};
                this.roleName = '개인';
                if (this.role == 'busi') {
                    item = {
                        name: '법인명',
                        id: 'busiName',
                        sort: false,
                        width: 120,
                    };
                    this.columns.unshift(item)
                    this.roleName = '법인';
                }
                item = {
                    name: '번호',
                    id: 'number',
                    sort: false,
                    width: 60,
                };
                this.columns.unshift(item)
                item = {
                    name: '선택',
                    id: 'id',
                    sort: false,
                    width: 60,
                    type: 'allCheckbox',
                };
                this.columns.unshift(item)
                this.searchReset();
                this.setDay('all');
                this.getDataList(1);
            },
            setColumn() {
                this.columns = [
                    {
                        name: '이름',
                        id: 'name',
                        width: 120,
                        sort: true,
                        textStyle: {
                            'text-align': 'left',
                            'padding-left': '10px'
                        },
                        type: 'underline-action',
                        action: 'memberView',
                    },
                    {
                        name: '아이디',
                        id: 'uid',
                        width: 120,
                        textStyle: {
                            'text-align': 'left',
                            'padding-left': '10px'
                        },
                        type: 'underline-action',
                        action: 'memberView',
                    },
                    {
                        name: '이메일',
                        id: 'email',
                        width: 120,
                    },
                    {
                        name: '닉네임',
                        id: 'nickName',
                        width: 100,
                    },
                    {
                        name: '이메일수신동의',
                        id: 'emailSendView',
                        width: 120,
                    },
                    {
                        name: '인증여부',
                        id: 'authText',
                        width: 100,
                    },
                    {
                        name: '탈퇴여부',
                        id: 'uoutText',
                        width: 120,
                    },
                    {
                        name: '수정일',
                        id: 'updateAt',
                        sort: true,
                        width: 120
                    },
                    {
                        name: '등록일',
                        id: 'createAt',
                        sort: true,
                        width: 120
                    },
                ];
            },
            setDay(cmd) {
                for (let key in this.dayClass) {
                    if (key == cmd) {
                        this.dayClass[key] = 'active';
                    } else {
                        this.dayClass[key] = '';
                    }
                }
                const nowDate = new Date();
                let mon = nowDate.getMonth() + 1;
                let day = nowDate.getDate();
                const nowDay = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                this.endate = nowDay;
                switch (cmd) {
                    case 'all':
                        this.stdate = '';
                        this.endate = '';
                        break;
                    case 'to':
                        this.stdate = nowDay;
                        break;
                    case 'yer':
                        nowDate.setDate(day - 1);
                        mon = nowDate.getMonth() + 1;
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'thr':
                        nowDate.setDate(day - 3);
                        mon = nowDate.getMonth() + 1;
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'week':
                        nowDate.setDate(day - 7);
                        mon = nowDate.getMonth() + 1;
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'mon':
                        nowDate.setMonth(mon - 1);
                        mon = nowDate.getMonth();
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'tmon':
                        nowDate.setMonth(mon - 3);
                        mon = nowDate.getMonth();
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;

                }
            },
            searchReset() {
                this.keywordCmd = 'name'
                this.keyword = '';
                this.stdate = '';
                this.endate = '';
                this.mstatus = 'all';
                this.emailSend = [];
                this.auth = [];
                this.dataSort.field = 'id'
                this.dataSort.sort = 'desc'
                this.datalimit = 20;
                this.page = 1;
                this.selectedId = '';
                this.selectedMember = {}
                this.memberDialog = false;


            },
            getSearchParams(page) {
                let emailSend = '';
                if (this.emailSend.length == 1) {
                    emailSend = this.emailSend[0];
                }
                let auth = '';
                if (this.auth.length == 1) {
                    auth = this.auth[0];
                }
                this.page = page;
                let frm = {
                    orderByField: this.dataSort.field,
                    orderBySort: this.dataSort.sort,
                    limit: this.datalimit,
                    mstatus: this.mstatus,
                    page: page,
                    keywordCmd: this.keywordCmd,
                    keyword: this.keyword,
                    stdate: this.stdate,
                    endate: this.endate,
                    emailSend: emailSend,
                    auth: auth,
                    role: this.role
                }
                return frm;
            },
            getDataList(page) {
                const frm = this.getSearchParams(page);
                apiService.post(API_ADMIN_GET_MEMBER_ALL_LIST, frm, (res) => {
                    this.setDataList(res.data);
                });
            },
            setDataList(datas) {
                this.memberList = [];
                this.paging = {
                    total: datas.totalElements,
                    currentPage: this.page,
                    pageGroup: 10,
                    limit: this.datalimit
                }
                const startNumber = parseInt(datas.totalElements) - (this.datalimit * (parseInt(this.page) - 1));
                let i = 0;
                for (let item of datas.content) {
                    item.number = startNumber - i;
                    item.createAt = item.createAt.replace("T", " ")
                    item.updateAt = item.updateAt.replace("T", " ")
                    item.emailSendView = (item.emailSend == 'yes') ? '동의' : '동의안함'
                    item.uoutText = (item.uout == 'yes') ? '탈퇴회원' : '정상회원'
                    item.authText = (item.auth == 'yes') ? '인증' : '미인증'
                    this.memberList.push(item)
                    i++;
                }
                this.tableShow = true;
            },
            pageMove(params) {
                this.getDataList(params.page);
            },
            tableEventAction(params) {
                switch (params.type) {
                    case 'dataSort':
                        this.dataSort = params.sort;
                        this.getDataList(1);
                        break;
                }

            },
            memberView(params) {
                this.selectedId = params.id;
                let selectedMember = this.memberList.filter(item => item.id == params.id)
                this.selectedMember = selectedMember[0];
                this.memberDialog = true;

            },
            search() {
                this.getDataList(1);
            },
            reload() {
                this.memberDialog = false;
                this.getDataList(this.page);
            },
            checkDataList(params) {
                this.ids = []
                for (let key in params) {
                    this.ids.push(key);
                }
            },
            excelDown() {
                let emailSend = '';
                if (this.emailSend.length == 1) {
                    emailSend = this.emailSend[0];
                }
                let auth = '';
                if (this.auth.length == 1) {
                    auth = this.auth[0];
                }
                let frm = {
                    orderByField: this.dataSort.field,
                    orderBySort: this.dataSort.sort,
                    limit: 10000000,
                    mstatus: this.mstatus,
                    page: 1,
                    keywordCmd: this.keywordCmd,
                    keyword: this.keyword,
                    stdate: this.stdate,
                    endate: this.endate,
                    emailSend: emailSend,
                    auth: auth,
                    role: this.role

                }
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_ADMIN_GET_MEMBER_ALL_LIST, frm, (res) => {
                    this.$eventBus.$emit('overlay', 'hide');
                    this.setExcelDown(res.data.content);
                });
            },
            setExcelDown(data) {
                this.excelList = [];
                for (let item of data) {
                    item.emailSendView = (item.emailSend == 'yes') ? '동의' : '동의안함'
                    item.nickName = (item.nickName) ? item.nickName : '';
                    item.uout = (item.uout == 'yes') ? '탈퇴회원' : '정상회원'
                    item.auth = (item.auth == 'yes') ? '인증' : '미인증'
                    this.excelList.push(item);
                }
                this.excelDownActive = true;


            },


            s2ab(s) {
                const buf = new ArrayBuffer(s.length); //convert s to arrayBuffer
                const view = new Uint8Array(buf);  //create uint8array as viewer
                for (var i = 0; i < s.length; i++) view[i] = s.charCodeAt(i) & 0xFF; //convert to octet
                return buf;
            },
            excelUpload() {
                this.$refs.file.click();
            },
            deleteMember() {
                if (this.ids.length < 1) {
                    let params = {
                        message: '삭제할 회원을 1개 이상 선택하세요'
                    }
                    this.$eventBus.$emit('modalOpen', params)
                    return;
                }
                let params = {
                    type: 'confirm',
                    message: '한번 삭제된 데이타는 복구가 불가능합니다<br>선택된 회원을 삭제하시겠습니까?',
                    doAction: () => {
                        this.$eventBus.$emit('overlay', 'open');
                        apiService.post(API_ADMIN_DELETE_MEMBER, {list: this.ids}, (res) => {
                            this.$eventBus.$emit('overlay', 'hide');
                            if (res.status == 'success') {
                                let params = {
                                    message: '정보가 삭제되었습니다'
                                }
                                this.$eventBus.$emit('modalOpen', params)
                                this.tableShow = false;
                                setTimeout(() => {
                                    this.tableShow = true;
                                    this.setInit();
                                }, 100)
                            }
                        })
                    }
                }
                this.$eventBus.$emit('modalOpen', params)

            },
            /*** 파일 업로드 ***/
            fileUpload(event) {
                let input = event.target;
                let reader = new FileReader();
                reader.onload = () => {
                    let data = reader.result;
                    let workBook = window.XLSX.read(data, {type: 'binary'});
                    workBook.SheetNames.forEach((sheetName) => {
                        let rows = XLSX.utils.sheet_to_json(workBook.Sheets[sheetName]);
                        let params = [];
                        let errorFlag = false;
                        for (let items of rows) {
                            let paramData = {};
                            let checkValidate = 0;
                            for (let key in items) {
                                let str = key.replace(/ /g, "");
                                switch (str) {
                                    case '아이디':
                                        checkValidate++;
                                        paramData.uid = items[key].toString();
                                        break;
                                    case '비밀번호':
                                        checkValidate++;
                                        paramData.upass = items[key].toString();
                                        break;
                                    case '이름':
                                        checkValidate++;
                                        paramData.name = items[key].toString();
                                        break;
                                    case '닉네임':
                                        paramData.nick = items[key].toString();
                                        break;
                                    case '이메일수신동의':

                                        if (items[key] == 'yes' || items[key] == 'no') {
                                            checkValidate++;
                                            paramData.emailSend = items[key].toString();
                                        }

                                        break;
                                    case '이메일':
                                        checkValidate++;
                                        paramData.email = items[key].toString();

                                        break;
                                    case '법인명':
                                        if (this.role == 'busi') {
                                            checkValidate++;
                                            paramData.busiName = items[key].toString();
                                        }
                                        break;
                                }

                            }
                            if (this.role == 'busi') {
                                if (checkValidate < 6) {
                                    errorFlag = true;
                                }
                            } else {
                                if (checkValidate < 5) {
                                    errorFlag = true;
                                }
                            }
                            params.push(paramData);
                        }
                        if (!errorFlag) {
                            this.$eventBus.$emit('overlay', 'open');
                            apiService.post(API_ADMIN_UPLOAD_MEMBER_EXCEL, {role: this.role, list: params}, (res) => {
                                this.$eventBus.$emit('overlay', 'hide');
                                if (res.status == 'success') {
                                    let params = {
                                        message: '정보가 저장되었습니다'
                                    }
                                    this.$eventBus.$emit('modalOpen', params)
                                    this.tableShow = false;
                                    setTimeout(() => {
                                        this.tableShow = true;
                                        this.setInit();
                                    }, 100)
                                } else {
                                    let msg = '';
                                    switch (res.code) {
                                        case 'doubleUid':
                                            msg = '중복된 아이디가 존재합니다'
                                            break;
                                        case 'doubleEmail':
                                            msg = '중복된 이메일이 존재합니다'
                                            break;
                                        case 'doubleNick':
                                            msg = '중복된 닉네임이 존재합니다'
                                            break;
                                    }
                                    let params = {
                                        message: msg
                                    }
                                    this.$eventBus.$emit('modalOpen', params)
                                }


                            })
                        } else {
                            let params = {
                                message: '필수입력값이 부족하거나 잘못된 입력정보입니다'
                            }
                            this.$eventBus.$emit('modalOpen', params)
                        }


                    })
                };
                reader.readAsBinaryString(input.files[0]);
                this.$refs.file.value = '';

            },

            /*** 파일 업로드 끝 ***/

        },
        updated() {
            if (this.excelDownActive) {
                const wb = window.XLSX.utils.book_new();
                const workSheet = window.XLSX.utils.table_to_sheet(document.getElementById('excelTable'));
                const fileName = '전체 ' + this.roleName + '회원 목록.xlsx';
                const sheetName = '전체 ' + this.roleName + '회원 목록';
                // step 3. workbook에 새로만든 워크시트에 이름을 주고 붙인다.
                window.XLSX.utils.book_append_sheet(wb, workSheet, sheetName);
                // step 4. 엑셀 파일 만들기
                const wbout = window.XLSX.write(wb, {bookType: 'xlsx', type: 'binary'});
                // step 5. 엑셀 파일 내보내기
                window.saveAs(new Blob([this.s2ab(wbout)], {type: "application/octet-stream"}), fileName);
                this.excelDownActive = false;
            }
        }

    }
</script>

